@import "vars";

$maxApplicationWidth: 1000px;
$minApplicationHeight: 860px;

$applicationWidth: 750px;
$minApplicationHeight: 1344px;

.gameApplicationWrapper {
  width: 100%;
  height: 100%;
}

.gameApplication {
  margin: auto;

  background-image: $appBackground;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;

  position: relative;

  color: #fff;

  .backgroundImage {
    width: 100%;
    height: 100%;

    position: absolute;
    top: 0;
    left: 0;
  }
}

.gameHistoryButton {
  position: absolute;
  z-index: 1;

  color: #fff;
  padding-top: 8px;
  font-weight: bolder;
  font-size: 14px;

  width: 140px;
  height: 60px;

  border: none;

  background: url("./images/gameActionBlankButton.png") center no-repeat;
  background-size: cover;

  &:hover {
    color: #31ff0f;
  }

}

.gameQuitButton {
  position: absolute;
  right: 0;
  z-index: 1;

  color: #fff;
  padding-top: 8px;
  font-weight: bolder;
  font-size: 14px;

  width: 140px;
  height: 60px;

  border: none;

  background: url("./images/quitGameBackground.png") center no-repeat;
  background-size: cover;

  &:hover {
    color: antiquewhite;
  }
}

.gameDesktop {

  width: 100%;
  height: 100%;
  background-image: $desktopBackground;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center 5px;

  position: relative;

  .gameDesktopContent {
    width: 100%;
    min-height: 500px;

    padding-top: 256px;
    text-align: center;
  }

}

.moneySelector {

  .moneySelectorButton {
    width: 100px;
    height: 48px;

    border: none;

    margin-top: 10px;

    background-color: transparent;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-image: $moneyButtonUrl;
    color: #61c3bb;
    font-weight: bolder;
    font-size: 20px;


    &:hover {
      background-image: $moneyButtonHoverUrl;
    }
  }

}

.popup {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;

  .popupBackdrop {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: #1b536c;
    z-index: 1;
    opacity: 0.7;

    min-height: 800px;
  }

  .popupWrapper {
    position: absolute;


    top: 80px;
    z-index: 2;

    width: 100%;

    .popupContainer {
      margin: auto;
      padding-top: 10px;

      background-image: url("./images/popup/wrapperBackground.png");
      background-color: transparent;
      background-repeat: no-repeat;
      background-size: 100% 100%;
    }

    .popupHeader {
      width: 100%;
      height: 30px;
      clear: both;

      .popupQuitButton {
        position: absolute;

        border: none;

        background-color: transparent;
        background-image: url("./images/fanhuijian.png");
        background-repeat: no-repeat;
        background-size: contain;
        width: 80px;
        height: 30px;

        text-indent: 1000px;
      }

      h2 {
        width: 100%;
        text-align: center;
        font-weight: bolder;
        font-size: 20px;
      }
    }

    .popupBody {
      position: relative;
      padding-top: 32px;
      padding-left: 28px;

      min-height: 250px;

      background-image: url("./images/popup/background.png");
      background-repeat: no-repeat;
      background-size: 100% 100%;

      .popupContent {
        width: 100%;
        height: 100%;

        position: absolute;
        left: 0;
        top: 0;
      }
    }
  }

}

.historyList {
  margin: auto;
  margin-top: 6px;
  width: 95%;
  border-spacing: 0;

  .historyNo {
    width: 50px;
  }

  .historyDate {
    width: 100px;
  }

  .historyAction {
    width: 20px;
  }

  .historyActionButton {
    background-color: transparent;
    background-image: url("./images/bofang.png");
    background-repeat: no-repeat;
    border: none;
    width: 30px;
    height: 30px;
    background-size: contain;
  }

  tr > td, tr > th {
    height: 30px;
    border: none;
    margin: 0;

    border-image: none;
  }

  tbody > tr:nth-child(2n) {
    background: darkslategrey;
  }
}

.userImage {
  width: 100px;
  height: 100px;
  display: inline-block;
  background-size: 100% 100%;
  background-color: transparent;

  @for $i from 1 to 15 {
    &.userImage#{$i} {
      background-image: url("./images/touxiang#{$i}.png");

      &.active {
        background-image: url("./images/touxiang#{$i}-active.png");
      }
    }
  }
}

.rankImage {
  width: 100px;
  height: 100px;
  display: inline-block;
  text-indent: 1900px;

  @for $i from 1 to 5 {
    &.rankImage#{$i} {
      background: url("./images/rank#{$i}.png");
    }
  }
}

.gameResult {
  margin: auto;
  margin-top: 6px;
  width: 95%;
  border-spacing: 0;

  .userImage, .rankImage {
    zoom: 0.4;
  }

  tbody > tr > td, tr > th {
    height: 30px;
    border: none;
    margin: 0;

    text-align: center;
  }

  tbody > tr:nth-child(2n) {
    background: darkslategrey;
  }
}

.gameAuthFormPopup {
  .popupBody {
    height: 250px;
  }

}

.gameAuthForm {
  margin: auto;
  margin-top: 18px;
  border-spacing: 0;

  hr {
    border-color: cadetblue;
    border-width: 1px;
    margin: 0 0 5px;
  }

  .gameAuthFormField {
    margin-bottom: 15px;
    padding-left: 20px;
    min-height: 30px;
    font-size: 12px;

    label > span {
      display: block;
      width: 100%;
      clear: both;
      padding-bottom: 10px;
    }

    input {
      display: block;
      width: 90%;
      height: 40px;
    }

    button {
      margin: auto;
      display: block;
      background-image: url("./images/authSubmitBtn.png");
      background-color: transparent;
      width: 163px;
      height: 60px;
      border: none;

      text-indent: -1000px;
      overflow: hidden;
    }
  }
}

.gameDesktopContent {
  position: relative;

  .playerInfo {
    position: absolute;

    $topBase: 200px;
    $leftBase: 300px;

    &.top {
      top: $topBase;
      left: 45%;
    }

    &.left {
      top: $topBase + 400px;
      left: 0;
    }

    &.right {
      top: $topBase + 400px;
      right: 0;
    }

    &.bottom {
      top: $topBase + 830px;
      left: 45%;
    }

    .userScore {
      display: inline-block;
      width: 55px;
      height: 63px;
      background-color: transparent;
      background-image: url("./images/scoreBackground.png");
      background-size: contain;
      background-repeat: no-repeat;

      top: -70px;
      left: 24px;
      position: absolute;

      font-size: 24px;
      text-align: center;
      font-weight: bolder;
      padding-top: 16px;
    }
  }
}