$rotateDuration: 250ms;

.boxGrid {
  width: 240px;
  margin: 0 auto;

  tr > td:first-child {
    padding-left: 18px;
  }

  td {
    padding: 8px 5px;
  }

  .box {
    background-repeat: no-repeat;
    background-image: url("./images/kapai01.png");
    background-size: cover;
    background-position: center;

    transition: transform $rotateDuration;
    transition-delay: $rotateDuration;

    &.box1 {
      background-image: url("./images/kapai01.png");
    }

    &.box2 {
      background-image: url("./images/kapai02.png");
    }

    &.box3 {
      background-image: url("./images/kapai03.png");
    }

    .score {
      width: 100%;
      height: 100%;
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;

      &.score0 {background-image: url("./images/score0.png")}
      &.score1 {background-image: url("./images/score1.png")}
      &.score2 {background-image: url("./images/score2.png")}
      &.score3 {background-image: url("./images/score3.png")}
      &.score4 {background-image: url("./images/score4.png")}
      &.score5 {background-image: url("./images/score5.png")}
      &.score6 {background-image: url("./images/score6.png")}
    }

    width: 80px;
    height: 106px;
    zoom: 0.58;
  }

}

.openBoxGamePage {
  .page__background{
    background: #081b32;
  }

}